            console.log('res',res);
            if(res.code === "0"){
              this.tableData = res.data;
              this.page.total = res.count;
            }
          })
      },
      // 重新渲染name列
      formatter(row, column, cellValue) {
        // return row.detail | ellipsis;
      },
      // 改变页面大小处理
      handleSizeChange(val) {

      },
      // 翻页处理
      handleCurrentChange(val) {

      },
      editTableData(row) {
        console.log('row',row);
      },
      //打开新建页面
      goAddAds(){
        this.$router.push({
          name:'AddAdministrators',
        })
      },
      //弹窗中确认按钮
      pSubmit(row) {

      },
      //打开详情弹窗
      detailPop(){
        this.$router.push({
          path: '/user/userMg/userInfo',
          name:'UserInfo'
        })
      }
    }

  }
</script>

<style lang="css" scoped>
  .cons{
    overflow: hidden;
  }
  .searchItem{
    margin-right: 32px;
  }
  .searchInput{
    width: 168px;
  }
  .searchSelect{
    width: 136px;
  }
  .searchCoBtn{
    margin-left: -20px;
  }
</style>



