<template>
  <div>
    <div class="centerTitle">
      新建管理员
    </div>
    <div class="formCon">
      <div class="formItem">
        <div class="label">管理员姓名：</div>
        <div class="input">
          <el-input v-model="realName" placeholder="请输入"></el-input>
        </div>
      </div>
      <div class="formItem">
        <div class="label">手机号：</div>
        <div class="input">
