        </div>
      </div>
      <div class="formItem">
        <div class="label">广告来源：</div>
        <div class="input">
          <el-radio v-model="role" label="Super_Admin">超级管理员</el-radio>
          <el-radio v-model="role" label="Admin">管理员</el-radio>
          <el-radio v-model="role" label="User">普通用户</el-radio>
        </div>
      </div>
      <div class="formBtnGroup">
        <el-button type="primary" class="CoBtn" @click="saveForm()">保存</el-button>
        <el-button class="CoResetBtn" @click="goPrePage()">返回上一页</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import SYSTEM from '@/service/system-service.js'
  export default {
    name: "AddAdministrators",
    data(){
      return{
        realName : '',
        phone : '',
        status : '',
        num : '',
        role: 'Admin'
      }
    },
    methods:{
      saveForm(){



        SYSTEM.setUserAdmin(data)
          .then(res => {
            console.log('res',res);
            if(res.code === "0"){

            }
          })
      }
    }
  }
</script>

<style lang="css" scoped>
  .totalCon{
    width: 95%;
  }
</style>



